/* Admin.css */
body {
    font-family: Arial, sans-serif;
    background-color: #f9f9f9;
    margin: 0;
    padding: 0;
  }
  
  .admin-container {
    max-width: 800px;
    margin: 20px auto;
    padding: 20px;
    background: #fff;
    box-shadow: 0px 4px 6px rgba(0, 0, 0, 0.1);
    border-radius: 8px;
  }
  
  .admin-title {
    text-align: center;
    margin-bottom: 20px;
    color: #333;
  }
  
  .admin-message {
    color: green;
    text-align: center;
    margin-bottom: 15px;
  }
  
  .admin-form-section {
    margin-bottom: 30px;
  }
  
  .admin-form h3 {
    margin-bottom: 10px;
  }
  
  .form-group {
    margin-bottom: 15px;
  }
  
  label {
    display: block;
    margin-bottom: 5px;
    font-weight: bold;
  }
  
  input, select {
    width: 100%;
    padding: 10px;
    margin: 0;
    border: 1px solid #ddd;
    border-radius: 4px;
    box-sizing: border-box;
  }
  
  input:focus, select:focus {
    border-color: #007bff;
    outline: none;
  }
  
  .admin-button {
    width: 100%;
    padding: 10px;
    background-color: #007bff;
    color: white;
    border: none;
    border-radius: 4px;
    cursor: pointer;
    font-size: 16px;
  }
  
  .admin-button:hover {
    background-color: #0056b3;
  }
  